/* Overlay styles */

.overlay-style-one {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	transition: all 700ms ease 100ms;
	opacity: 0;
	z-index: 2;
}

.overlay-style-one:before {
	position: absolute;
	content: '';
	left: 0;
	top: 0;
	right: 0;
	bottom: 0;
	opacity: .9;
}

.overlay-style-one .box {
	display: table;
	height: 100%;
	width: 100%;
}

.overlay-style-one .box .content {
	display: table-cell;
	text-align: center;
	vertical-align: middle;
}

#shop-area {
	padding-top: 110px;
	padding-bottom: 110px;
}

.main-shop-area {
	position: relative;
	display: block;
	background: #ffffff;
}

#shop-area .shop-content {
	position: relative;
	display: block;
}

#shop-area .ui-selectmenu-button.ui-button {
	color: #222;
	font-family: inherit;
	text-transform: capitalize;
	border-color: #e7e7e7;
	padding: 15px 20px;
}

.showing-result-shorting {
	display: flex;
	justify-content: space-between;
	flex-direction: row;
	flex-wrap: wrap;
	align-items: center;
	padding-bottom: 30px;
	border-bottom: 1px solid #ededed;
	margin-bottom: 50px;
}

.showing-result-shorting .showing p {
	color: #282828;
	font-size: 16px;
	font-weight: 700;
	margin: 0;
}

.single-product-item {
	position: relative;
	display: block;
	transition: all 500ms ease;
	margin-bottom: 50px;
}

.single-product-item .img-holder {
	position: relative;
	display: block;
	overflow: hidden;
}

.single-product-item .img-holder img {
	-webkit-transform: scale(1);
	transform: scale(1);
	transition: all 0.7s ease 0s;
	width: 100%;
}

.single-product-item:hover .img-holder img {
	-webkit-transform: scale(1.1);
	transform: scale(1.1);
}

.single-product-item .title-holder {
	position: relative;
	display: block;
	background: #ffffff;
	z-index: 10;
}

.single-product-item .title-holder .static-content {
	position: relative;
	display: block;
	text-align: left;
	padding: 30px 0 0;
}

.single-product-item .title-holder .title {
	position: relative;
	display: block;
	font-size: 24px;
	line-height: 22px;
	font-weight: 700;
}

.single-product-item .title-holder .title a {
	color: #282828;
	transition: all 500ms ease;
}

.single-product-item .title-holder .static-content span {
	color: #828282;
}

.single-product-item .price {
    position: absolute;
    top: 30px;
    right: 30px;
    background: #222;
    color: #fff;
    font-size: 16px;
    font-weight: 700;
    padding: 2px 15px;
    border-radius: 15px;
}

.single-product-item .overlay-btn {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: .5s;
    -webkit-transform: scale(.5);
            transform: scale(.5);
    opacity: 0;
}

.single-product-item:hover .overlay-btn {
	opacity: 1;
	-webkit-transform: scale(1);
	        transform: scale(1);
}

.single-product-item .category {
    position: relative;
    font-size: 14px;
    font-weight: 700;
    margin-bottom: 10px;
}

.single-product-item .rating {
    position: absolute;
    right: 0;
    top: 30px;
    font-size: 15px;
}

.sidebar-wrapper {
	position: relative;
	display: block;
	max-width: 270px;
	width: 100%;
}

.sidebar-wrapper .single-sidebar-box {
	position: relative;
	display: block;
	overflow: hidden;
}

.single-sidebar-box {
	border: 2px solid #ebebeb;
	padding: 30px;
	margin-bottom: 40px;
}

.single-sidebar-box-two {
	margin-bottom: 40px;
}

.showing-result-shorting .shorting select.selectmenu {
    border: 2px solid #ebebeb;
    height: 50px;
    padding: 0 20px;
    font-size: 16px;
    font-weight: 700;
}

.sidebar-wrapper .search-form {
	position: relative;
	display: block;
	max-width: 270px;
	width: 100%;
	margin-bottom: 50px;
}

.sidebar-wrapper .search-form input[type="text"] {
	position: relative;
	display: block;
	background: #fff;
	border: 2px solid #ebebeb;
	color: #282828;
	font-size: 15px;
	font-weight: 700;
	height: 50px;
	max-width: 270px;
	width: 100%;
	padding-left: 40px;
	padding-right: 20px;
	transition: all 500ms ease 0s;
}

.sidebar-wrapper .search-form button {
	position: absolute;
	top: 0;
	left: 4px;
	bottom: 0;
	display: block;
	color: #282828;
	background: transparent;
	font-size: 14px;
	height: 50px;
	width: 50px;
	border: 0px solid #edf2f7;
	line-height: 48px;
	text-align: center;
	transition: all 500ms ease 0s;
	margin: 0;
}

.sidebar-wrapper .search-form button i {
	font-size: 14px
}

.sidebar-wrapper .search-form input[type="text"]:focus {
	background: #fff;
	color: #000000;
